import React, { useEffect, useState } from 'react';
import Layout from '../components/layout';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import HorizontalAds from '../components/horizontal-ads'

const CodesandboxTemplate = ({ data, location }) => {
  const [isDisplay, setIsDisplay] = useState(true);
  const [iframeHtml, setIframeHtml] = useState("");
  const { html, frontmatter } = data.markdownRemark;

  const params = new Proxy(new URLSearchParams(location.search), {
    get: (searchParams, prop) => searchParams.get(prop),
  });

  useEffect(() => {
    setInterval(() => {
      setIsDisplay(false)
      setIsDisplay(true)
    }, 30000)
  }, []);

  useEffect(() => {
    if (params.code) {
      let iframeHtml = `<iframe src='https://codesandbox.io/embed/${params.code}?fontsize=14&theme=dark'
      style="width:100%; height:500px; border:0; border-radius: 4px; overflow:hidden;"
      title='${params.code}'
      loading="lazy"
      allow="accelerometer; ambient-light-sensor; camera; encrypted-media; geolocation; gyroscope; hid; microphone; midi; payment; usb; vr; xr-spatial-tracking"
      sandbox="allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts"
      ></iframe>`;
      setIframeHtml(iframeHtml);
    } else {
      let iframeHtml = `<iframe src='https://codesandbox.io/embed/quirky-ellis-8olfq4?fontsize=14&theme=dark'
      style="width:100%; height:500px; border:0; border-radius: 4px; overflow:hidden;"
      title='quirky-ellis-8olfq4'
      loading="lazy"
      allow="accelerometer; ambient-light-sensor; camera; encrypted-media; geolocation; gyroscope; hid; microphone; midi; payment; usb; vr; xr-spatial-tracking"
      sandbox="allow-forms allow-modals allow-popups allow-presentation allow-same-origin allow-scripts"
      ></iframe>`;
      setIframeHtml(iframeHtml);
    }
  }, [params.code]);

  return (
    <Layout title={frontmatter.title}>
      <link rel="canonical" href="https://aguidehub.com/codesandbox/"></link>
      <PostWrapper>
        <PostContent dangerouslySetInnerHTML={{ __html: html }} />
        {isDisplay && <HorizontalAds />}
        <PostContent dangerouslySetInnerHTML={{ __html: iframeHtml }} />
        {isDisplay && <HorizontalAds />}
      </PostWrapper>
    </Layout>
  );
};

export default CodesandboxTemplate;

const PostWrapper = styled.div`
  padding-top: var(--size-900);
  padding-bottom: var(--size-900);
  margin-left: auto;
  margin-right: auto;
  word-wrap: break-word;
  flex-direction: column;
  display: flex;
`;

const PostContent = styled.section`
  padding-top: var(--size-800);

  & > * + * {
    margin-top: var(--size-300);
  }

  & > p + p {
    margin-top: var(--size-700);
  }

  * + h1,
  * + h2,
  * + h3 {
    margin-top: var(--size-900);
  }

  h1 {
    font-size: var(--size-800);
  }

  h2 {
    font-size: var(--size-700);
  }

  h3 {
    font-size: var(--size-700);
  }

  b,
  strong {
    font-weight: 600;
  }

  a {
    color: inherit;
    text-decoration: underline;
    text-decoration-thickness: 0.125rem;
  }

  blockquote {
    padding-left: var(--size-400);
    border-left: 5px solid;
    font-style: italic;
  }

  code {
    font-family: 'Source Sans Pro', monospace;
    overflow-x: auto;
    white-space: pre-wrap;
  }

  pre {
    overflow-x: auto;
    white-space: pre-wrap;
    max-width: 100%;
  }
`;

export const pageQuery = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        profile_image
      }
    }
  }
`;
